@charset "utf-8";
/* movie.css */

@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@100..900&display=swap');

* {
  margin: 0px;
  padding: 0px;
}

body {
  font-family: "Noto Sans", sans-serif;
  font-size: 13px;
  color: #333;
  background: #eff3f7;
}

a {
  color: #333;
  text-decoration: none;
  font-size: 16px;
}

ul li {
  list-style: none;
}

/* 상단 헤더 서식 */
.header {
  width: 200px;
  height: 100%;
  text-align: center;
  position: fixed;
  top: 0px;
  left: 0px;
  z-index: 100;
}

h1 {
  padding: 70px 0px 50px 0px;
}

.navi {
  line-height: 40px;
}

.navi a {
  text-transform: uppercase;
  font-weight: bold;
}

.navi a:hover {
  text-decoration: underline;
}


/* 메인서식 */
main {
  width: calc(100% - 200px);
  height: 100%;
  position: fixed;
  left: 200px;
  top: 0px;
  padding-top: 50px;
  overflow: scroll;
}

/* 공통서식 */

/* nav서식 */
nav {
  padding-top: 70px;
  text-align: center;
}

.nav li {
  line-height: 40px;
}

.nav a {
  text-decoration: none;
  font-size: 16px;
  padding: 5px 10px;
  color: #5d5d5d;
  text-shadow: 0px 1px 1px #adadad;
}


/* footer서식 */
.footer {
  width: 100%;
  line-height: 40px;
  background: #333;
}

.footer address {
  color: #fff;
  text-align: center;
  font-style: normal;
}

/* 가로 해상도 1025px일 경우 서식 */
@media screen and (min-width:1025px) {
  main  .movie_detail{
    display:grid;
    grid-template-columns: repeat(2, 1fr);
    gap:10px;
  }
  main .movie_detail li{
    background-color: #fff;
    box-shadow: 0 6px 18px rgba(0,0,0,0.12);
    width:90%; height: 400px;

  }
  main .movie_detail li .m_id{display:none;}
  main .movie_detail li .m_year{}
  main .movie_detail li .m_title{}
  main .movie_detail li .m_summary{}
  main .movie_detail li .m_img{}
  main .movie_detail li .m_genres{}
  main .movie_detail li .m_rating{}
}

